	module.exports = {"data":{"post":{"html":"<p>I'm with <a title=\"Heiko Hebig | hebig.com | Oh this is for REAL?\" href=\"http://www.hebig.com/archives/000983.html\">Heiko Hebig</a> on this issue. If we're going to have Freedom Fries and Freedom Toast, we cannot stop there. If we are really going to do the weasels in, we need Freedom Burgers instead of Hamburgers, and Freedom Weenies instead of Frankfurters. That is, until the US Congress starts reading the <a href=\"http://mailbox.univie.ac.at/~prillih3/blog/\">Aardvark</a>, then the Weenies will have to go as well. Freedom Sausages? Wait, aren't those Vienna Sausages?</p>\n<p>And what about <a href=\"http://allrecipes.iwon.com/encyc/terms/B/5486.asp\">Braunschweiger</a>?</p>","fields":{"title":"Freedom Tofu","date":"13 Mar 2003","url":"https://papascott.de/2003-03-13-freedom-tofu/"}}},"pathContext":{"slug":"/2003-03-13-freedom-tofu/","prev":{"fields":{"title":"Illegal Exports","date":"12 Mar 2003","slug":"/2003-03-12-illegal-exports/"}},"next":{"fields":{"title":"Here We Are","date":"14 Mar 2003","slug":"/2003-03-14-here-we-are/"}}}}

